import { Component, OnInit } from '@angular/core';
import { IMService, IMEventTypes, NotifTypes } from '../../services/im.service';
import { environment } from '../../../environments/environment';
import { AuthService } from '../../services/auth.service';
import { DalService } from '../../services/dal.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  isAlreadySignedUp = false;
  constructor(private im: IMService, private auth: AuthService, private dal: DalService, private router: Router) { }

  ngOnInit() {
    // this.dal.checkPaypalPayment().subscribe(x => {
    //   this.isAlreadySignedUp = x && x[0] && x[0]._id
    // })
  }

  startPaypal(amount, courseCount) {
    if (!this.auth.isLoggedIn) {
      this.router.navigateByUrl('/login')
      this.im.publish(IMEventTypes.NOTIFICATION, { msg: 'Please login/create account first, then you can make payments', type: NotifTypes.ERROR })
      return;
    }

    if (this.isAlreadySignedUp) {
      this.im.publish(IMEventTypes.NOTIFICATION, { msg: 'You are already signed up with us, you can go to courses section', type: NotifTypes.ERROR })
      return;
    }
    console.log(amount, courseCount)
    this.im.publish(IMEventTypes.BUY, {
      amount,
      currency: environment.paypal.currency,
      courseCount
    })
  }

}
