import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, CanActivate } from '@angular/router';
import { Observable } from 'rxjs';
import { AuthService } from '../services/auth.service';
import { IMService, IMEventTypes, NotifTypes } from '../services/im.service';

@Injectable({
  providedIn: 'root'
})
export class LearningGuard implements CanActivate {
  constructor(private auth: AuthService, private im: IMService) { }
  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean | UrlTree | Observable<boolean | UrlTree> | Promise<boolean | UrlTree> {
    const isLoggedIn = this.auth.isLoggedIn;
    if (!isLoggedIn) {
      this.im.publish(IMEventTypes.NOTIFICATION, { msg: 'Please login to learn', type: NotifTypes.ERROR })
    }

    return isLoggedIn;
  }

}
