import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { DalService } from '../../../services/dal.service';
import { environment } from '../../../../environments/environment';
import { AuthService } from '../../../services/auth.service';
import { NotifTypes, IMEventTypes, IMService } from '../../../services/im.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-video-player',
  templateUrl: './video-player.component.html',
  styleUrls: ['./video-player.component.css']
})
export class VideoPlayerComponent implements OnInit {
  @ViewChild('videoPlayer', { static: true }) videoplayer: ElementRef;
  state = {
    isPlaying: false,
    progress: 0,
    courseId: 1,
    videoLink: '',
    shouldWait: true,
    videoDetails: { title: '', details: '', videoId: '' }
  }
  lectures: any[] = [];
  nativePlayer: any;
  constructor(private route: ActivatedRoute, private dal: DalService, private auth: AuthService, private im: IMService) {

  }

  ngOnInit() {

    this.route.params.subscribe(x => {
      this.dal.getCourses(`/courses/details/${x.title}`).subscribe(y => {

        const course = y.length ? y[0] : y;
        this.state.courseId = course._id;
        this.dal.getCourseContents(this.state.courseId).subscribe(contents => {
          if (!contents) {
            alert('No course details found');
            return;
          }
          contents = contents[0] || contents;
          this.lectures = contents.videos;
          if (contents.videos && contents.videos.length) {
            this.state.videoLink = `${environment.videoLink}/${this.state.courseId}/${contents.videos[0].videoId}?t=${this.auth.userToken}`
            this.state.shouldWait = false;
            this.state.videoDetails = contents.videos[0];
            this.playPauseVideo();
          }
        });
      });
    });
  }


  changeVideoLink(videoId, l) {
    this.state.isPlaying = false;
    if (this.nativePlayer)
      this.nativePlayer.pause();
    this.state.videoLink = `${environment.videoLink}/${this.state.courseId}/${videoId}?t=${this.auth.userToken}`
    this.state.videoDetails = l;
    console.log(this.state, this.nativePlayer)
  }
  playPauseVideo() {
    if (!this.nativePlayer) {
      this.nativePlayer = this.videoplayer.nativeElement;
    }

    if (!this.state.isPlaying) {
      this.nativePlayer.play();
      this.state.isPlaying = true;
    } else {
      this.nativePlayer.pause();
      this.state.isPlaying = false;
    }
    console.log('>>>>>', this.state, this.nativePlayer)
    this.nativePlayer.addEventListener('timeupdate', ($event) => {
      this.state.progress = (this.nativePlayer.currentTime / this.nativePlayer.duration) * 100;

    })

    this.nativePlayer.addEventListener('error', ($event) => {
      console.log("Error while playing", $event);
      this.im.publish(IMEventTypes.NOTIFICATION, { msg: 'Unable to play video, please login first', type: NotifTypes.SUCCESS })
    }, true)

    this.nativePlayer.addEventListener('waiting', ($event) => {
      // alert('ok')
    })

  }
  seekVideo($event) {
    const bar: any = document.getElementsByClassName('.progress-bar')[0];
    if (bar) {
      const total = bar.offsetWidth;
      this.state.progress = total / $event.offsetX;
      console.log(this.state, bar, total)
    }


  }
}
