import { Component, OnInit } from '@angular/core';
import { DalService } from '../../services/dal.service';
import { IMService, IMEventTypes, NotifTypes } from '../../services/im.service';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  isLoginMode: boolean = true;

  register = {
    name: '',
    userId: '',
    password: '',
    confirm: ''
  }
  login = {
    userId: '',
    password: ''
  }
  msg: string = null;
  constructor(private dal: DalService, private im: IMService, private auth: AuthService, private router: Router) { }

  ngOnInit() {
    console.log(this.auth.userToken, this.auth.isLoggedIn)
  }

  toggle(x) {
    this.isLoginMode = !this.isLoginMode;
  }

  attemptLogin() {
    this.dal.loginUser(this.login).subscribe(x => {

      if (x.token) {
        this.auth.userToken = x.token;
        this.auth.personName = x.name;
        this.im.publish(IMEventTypes.NOTIFICATION, { msg: 'Login successful', type: NotifTypes.SUCCESS })
        this.router.navigateByUrl('/courses/list')
      } else {
        this.im.publish(IMEventTypes.NOTIFICATION, { msg: 'Login failed', type: NotifTypes.ERROR })
      }
    })
  }

  attemptRegister() {

    this.dal.registerUser(this.register).subscribe(x => {
      console.log(x)
      if (x._id) {
        this.im.publish(IMEventTypes.NOTIFICATION, { msg: ' Registration Successful, please login with these details', type: NotifTypes.SUCCESS })
      } else {
        if (x.code === 11000) {
          this.im.publish(IMEventTypes.NOTIFICATION, { msg: ' This email id is already registered, please login', type: NotifTypes.ERROR })
        }
        else {
          this.im.publish(IMEventTypes.NOTIFICATION, { msg: ' Registration failed', type: NotifTypes.ERROR })
        }
      }
    })
  }

}
