import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { LoginComponent } from './components/login/login.component';
import { VideoPlayerComponent } from './components/learn/video-player/video-player.component';
import { LearningGuard } from './guards/learning.guard';
import { MdWritterComponent } from './components/md-writter/md-writter.component';
import { WriterProfileComponent } from './components/profiles/writer-profile/writer-profile.component';
import { EditorComponent } from './components/editor/editor.component';
import { CheckoutComponent } from './components/checkout/checkout.component';
import { LearnPersonalizedComponent } from './components/learn/learn-personalized/learn-personalized.component';

const routes: Routes = [
  {
    path: '', component: HomeComponent,

  }, {
    path: 'null', component: HomeComponent,

  }, {
    path: 'login', component: LoginComponent
  },
  {
    path: 'checkout', component: CheckoutComponent
  },
  {
    path: 'courses', loadChildren: './modules/course/course.module#CourseModule'
  },

  {
    path: 'series', loadChildren: './modules/articleseries/articleseries.module#ArticleseriesModule'
  },
  {
    path: 'writer', component: MdWritterComponent
  },
  {
    path: 'editor', component: EditorComponent
  },
  {
    path: 'writer/:articleId', component: MdWritterComponent
  },
  {
    path: 'writer-profile', component: WriterProfileComponent
  },
  {
    canActivate: [LearningGuard],
    path: 'learn/:title', component: VideoPlayerComponent
  },
  {
    canActivate: [LearningGuard],
    path: 'learn-personalized/:title', component: LearnPersonalizedComponent
  }
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
