import { Injectable } from '@angular/core';
import { CustomHttpService } from './custom-http.service';
import { environment } from '../../environments/environment';
import { AuthService } from './auth.service';

@Injectable({
  providedIn: 'root'
})
export class DalService {

  constructor(private cHTTP: CustomHttpService, private auth: AuthService) { }

  getFromJSON() {
    return this.cHTTP.getReq(environment.jsonBlobPath);
  }

  getCoursePaymentVerification(courseId) {
    return this.cHTTP.getReq(environment.coursePaymentVerificationURL, { courseId })
  }
  processPayment(courseId, courseAmount, courseLink) {
    this.cHTTP.openWin(environment.processPaymentURL, { courseId: btoa(courseId), courseAmount: btoa(courseAmount), t: this.auth.userToken || 'no-token', courseLink: btoa(courseLink) })
  }
  saveJSONBlob(completeJSON: any) {
    return this.cHTTP.postReq(environment.jsonBlobPath, completeJSON);
  }

  getBanner() {
    return this.cHTTP.getReq(environment.bannerURL)
  }

  getAbout() {
    return this.cHTTP.getReq(environment.aboutURL)
  }

  getCourseContents(courseId) {
    return this.cHTTP.getReq(`${environment.courseContentsURL}/${courseId}`)
  }

  getPosts(routerLink?: string) {
    let q = null;
    if (routerLink) {
      q = { routerLink }
    }
    return this.cHTTP.getReq(environment.postsURL, q)
  }
  searchArticle(searchQuery) {
    return this.cHTTP.getReq(`${environment.searchArticles}`, { searchQuery });
  }
  getCourses(routerLink?: string) {
    let q = null;
    if (routerLink) {
      q = { routerLink }
    }
    return this.cHTTP.getReq(environment.coursesURL, q)
  }


getPersonalizedCourse(query){
  return this.cHTTP.getReq(environment.coursesURL, query)
}

  postComment(comment) {
    return this.cHTTP.postReq(environment.commentsURL + '/save', comment)
  }
  getUserDetails(filter = null) {
    return this.cHTTP.postReq(environment.usersURL + '/list', filter)
  }
  updateUserProfile(payload) {
    return this.cHTTP.putReq(environment.usersURL + '/update', payload, null)
  }

  getComments(query) {
    return this.cHTTP.getReq(environment.commentsURL + '/list', query);
  }

  registerUser(o) {
    return this.cHTTP.postReq(environment.registerURL, o);
  }

  loginUser(o) {
    return this.cHTTP.postReq(environment.loginURL, o);
  }

  postPaypalSuccess(payload) {
    return this.cHTTP.postReq(environment.paypalURL + '/add', payload);
  }

  checkPaypalPayment() {
    // auto header check
    return this.cHTTP.postReq(environment.paypalURL + '/list', {});
  }

  getArticles(query?) {

    return this.cHTTP.getReq(environment.articlesURL + '/list', query);
  }

  getSeriesList() {
    return this.cHTTP.getReq(environment.seriesListURL);
  }
  getDocumentAnalysis(blogData) {
    return this.cHTTP.postReq(environment.analysisURL, { blogData });
  }

  saveArticle(payload) {
    console.log('upsert ?', payload)
    if (payload._id) {
      const query = { articleId: payload._id }
      const updatePayload = {};
      Object.keys(payload).forEach(k => {
        if (k !== '_id') {
          updatePayload[k] = payload[k];
        }
      })

      return this.cHTTP.putReq(environment.articlesURL + '/update', payload, query)
    } else {
      return this.cHTTP.postReq(environment.articlesURL + '/save', payload)
    }

  }

  saveArticleForEditor(payload) {
    console.log('payload for save on editor', payload)
    if (payload._id) {
      const query = { articleId: payload._id }
      const updatePayload = {};
      Object.keys(payload).forEach(k => {
        if (k !== '_id') {
          updatePayload[k] = payload[k];
        }
      })

      return this.cHTTP.putReq(environment.articlesURL + '/update', payload, query)
    } else {
      return this.cHTTP.postReq(environment.articlesURL + '/saveForEditor', payload)
    }

  }

  deleteArticle(articleId) {
    return this.cHTTP.deleteReq(environment.articlesURL + '/remove', { articleId })
  }

  articleStats(articleId, key) {
    return this.cHTTP.getReq(environment.articlesURL + '/stats', { articleId, key })
  }

  getMdFile(routerLink) {
    return this.cHTTP.getReq(environment.articlesURL + '/list', { routerLink })
  }

}
