import { Injectable, destroyPlatform } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { environment } from '../../environments/environment';
import { SocialShareService, SharingProvider } from './social-share.service';

@Injectable({
  providedIn: 'root'
})
export class SeoService {

  constructor(private title: Title, private meta: Meta, public socialShareService: SocialShareService) { }

  setMDFileSEO(mdFileContents) {
    this.setMetaTags({ ...mdFileContents, blogData: null });

  }

  setMetaTags(o: any, shouldDerive = true) {
    const skipProps = ['blogData', '_id', '__v', 'routerLink'];
    if (shouldDerive) {

      for (const key in o) {
        if (skipProps.indexOf(key) !== -1) { }
        else {
          ['og', 'twitter'].forEach(x => {
            o[x + ':' + key] = ('' + o[key]).trim();
          })

        }

      }
    }
    Object.keys(o).forEach(x => {
      try {
        this.meta.removeTag(x);
      } catch (error) { }

    })

    this.title.setTitle(o.title);
    this.meta.addTags(Object.keys(o).map((y) => {

      if (skipProps.indexOf(y) !== -1) { }
      else {
        return { name: y, content: o[y] }
      }

    }));
  }

  // Parser dont touch later
  parse(mdFileContents) {
    let o: any = { ...environment.seo };
    let metaData = mdFileContents.match(/<!--(.*?)-->/g)
    if (metaData && metaData.length) {
      metaData = metaData.map(x => {
        x = x.replace('<!--', '').replace('-->', '').trim();
        const splitted = x.split(':::');

        o[splitted[0]] = splitted[1].trim();
        ['og', 'twitter'].forEach(x => {
          o[x + ':' + splitted[0]] = splitted[1].trim();
        });
        return x;
      });

    } else {
      const dseo = this.getDefaultSEO()


      o = { ...o, ...dseo };
    }
    console.log(o)
    return o;
  }


  getDefaultSEO() {
    const dseo = {
      title: 'ProCo | The professional courses platform and article series for your deep learning',
      description: 'ProCo believes in deep concept learning and architecture explorations in different technologies, with article series and courses.',
      image: 'https://proco.guru/assets/img/vector/22.png',
      url: 'https://proco.guru',
      keywords: 'ProCo, courses, deep learning articles, technology blogs'
    }
    return dseo;
  }
}
