import { Component, OnInit, PLATFORM_ID, Inject } from '@angular/core';
import { DalService } from './services/dal.service';
import { IMService, IMEventTypes } from './services/im.service';
import { AuthService } from './services/auth.service';
import { environment } from '../environments/environment';
// declare ga as a function to set and sent the events

import { Router, NavigationStart, NavigationEnd } from '@angular/router';
import { SeoService } from './services/seo.service';
declare let ga: Function;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  msgObj: any = { msg: '' };
  showBuyModal = false;
  paypalTransaction = {
    amount: 10,
    currency: environment.paypal.currency,
    courseCount: 2
  }
  promoObj: any = {};
  currRoute;
  constructor(private im: IMService, private router: Router, private seo: SeoService, public auth: AuthService, @Inject(PLATFORM_ID) private platformId: Object) {
  }


  ngOnInit() {
    this.router.events.subscribe((x: NavigationStart) => {
      if (x instanceof NavigationStart && (x.url || '').indexOf('article') === -1) {
        console.log(x)

        this.seo.setMetaTags(this.seo.getDefaultSEO())
      }
      if (x instanceof NavigationEnd) {
        this.currRoute = x.urlAfterRedirects;
        ga('set', 'page', x.urlAfterRedirects);
        ga('send', 'pageview');

      }

    })

    this.im.subs(IMEventTypes.NOTIFICATION).subscribe(x => {
      this.msgObj = x;
      setTimeout(() => {
        this.msgObj = {};
      }, 5000);
    });

    this.im.subs(IMEventTypes.BUY).subscribe(x => {
      if (x.currency && x.courseCount && x.amount)
        this.startBuying(x);
    });
    this.im.subs(IMEventTypes.PROMO).subscribe(x => {
      this.promoObj = x;
    });
    // if (isPlatformBrowser(this.platformId)) {
    //   const localDB = JSON.parse(localStorage.getItem('db'));
    //   this.dal.getFromJSON().subscribe(x => {
    //     if (!localDB || localDB.version !== x.version) {
    //       localStorage.setItem('db', JSON.stringify(x));
    //       localStorage.removeItem('fetchDb')
    //       console.warn('version mismatched, DB updated at local')
    //       window.location.reload();
    //     }

    //   })
    // }
  }

  startBuying(x) {
    this.showBuyModal = true;
    this.paypalTransaction = x;
  }
  mobileNavLeft = -300;

  toggle() {
    if (this.mobileNavLeft === 0) {
      this.mobileNavLeft = -300;
    } else {
      this.mobileNavLeft = 0;
    }

  }
}
