import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpErrorResponse } from '@angular/common/http';
import { AuthService } from './auth.service';
import { Router } from '@angular/router';
import { IMService, IMEventTypes, NotifTypes } from './im.service';
import { of, throwError } from 'rxjs';
import "rxjs/add/operator/do";

@Injectable()
export class CustomHTTPInterceptor implements HttpInterceptor {

    constructor(private auth: AuthService, private router: Router, private im: IMService) { }

    intercept(req, next) {

        const authReq = req.clone({
            setHeaders: {
                'x-access-token': this.auth.userToken || 'no-token'
            }
        });

        console.log('Intercepted HTTP call', authReq);

        return next.handle(authReq).do(
            event => {

                //end loading
            },
            (error: any) => {
                if (error instanceof HttpErrorResponse) {
                    if (error.status == 403) {
                        this.router.navigateByUrl(`/login`);
                        // if you've caught / handled the error, you don't want to rethrow it unless you also want downstream consumers to have to handle it as well.
                        this.im.publish(IMEventTypes.NOTIFICATION,
                            { msg: 'Please login to continue', type: NotifTypes.ERROR })
                        this.auth.logout();
                    }

                }

                //end loading
            }
        );

    }
    handleError(err) {

        //handle your auth error or rethrow
        if (err.status === 401 || err.status === 403) {
            //navigate /delete cookies or whatever
            this.router.navigateByUrl(`/login`);
            // if you've caught / handled the error, you don't want to rethrow it unless you also want downstream consumers to have to handle it as well.
            this.im.publish(IMEventTypes.NOTIFICATION,
                { msg: 'Please login to continue', type: NotifTypes.ERROR })
            this.auth.logout();
            return of(err.message); // or EMPTY may be appropriate here

        }
        return throwError(err);

    }
}

