import { Injectable, PLATFORM_ID, Inject } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private token = null;
  private name;
  constructor(@Inject(PLATFORM_ID) private platformId: Object) {
    if (isPlatformBrowser(this.platformId)) {
      this.token = localStorage.getItem('t');
      this.name = localStorage.getItem('n');
    }
  }

  getAuthorDetails() {
    if (isPlatformBrowser(this.platformId)) {
      return localStorage.getItem('a') || {};
    }
  }

  saveBlogDraft(draft) {
    if (isPlatformBrowser(this.platformId)) {
      localStorage.setItem('blog', JSON.stringify(draft));
    }
  }

  getBlogDraft() {
    if (isPlatformBrowser(this.platformId)) {
      try {
        return JSON.parse(localStorage.getItem('blog'));
      } catch (e) {
        return null;
      }

    }
  }

  get userToken() {
    return this.token;
  }

  get isLoggedIn(): boolean {
    return this.userToken ? true : false;
  }

  set userToken(token) {
    this.token = token;
    if (this.token)
      this.saveToken();
  }

  private saveToken() {
    this.setCookie('x-access-token', this.userToken, 1);
    if (isPlatformBrowser(this.platformId)) {
      localStorage.setItem('t', this.userToken)
    }
  }

  get personName() {
    return this.name;
  }

  set personName(name) {
    this.name = name;
    if (name)
      if (isPlatformBrowser(this.platformId)) {
        localStorage.setItem('n', name);
      }
  }

  logout() {
    if (isPlatformBrowser(this.platformId)) {
      localStorage.removeItem('t');
      localStorage.removeItem('n');
    }
    this.personName = null;
    this.userToken = null;
  }

  private setCookie(cname, cvalue, exdays) {
    var d = new Date();
    d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
    var expires = "expires=" + d.toUTCString();
    document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
  }
}
