import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class IMService {

  private eventBuses: Map<IMEventTypes, BehaviorSubject<any>> = new Map();
  constructor() { }

  private get(type: IMEventTypes) {

    const bs = this.eventBuses.get(type);
    if (!bs) {
      this.eventBuses.set(type, new BehaviorSubject({}));
    }
    return this.eventBuses.get(type);
  }

  publish(type: IMEventTypes, dataToPublish: any) {
    this.get(type).next(dataToPublish);
  }

  subs(type: IMEventTypes) {
    return this.get(type);
  }
}


export enum IMEventTypes {
  NOTIFICATION = 'NOTIFICATION',
  BUY = 'BUY',
  PROMO = 'PROMO'
}

export enum NotifTypes {
  MESSAGE = 'MESSAGE',
  ERROR = 'ERROR',
  WARNING = 'WARNING',
  SUCCESS = 'SUCCESS'
}