import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { FooterComponent } from './components/footer/footer.component';
import { HomeComponent } from './components/home/home.component';
import { HttpClientModule, HTTP_INTERCEPTORS, HttpClient } from '@angular/common/http';
import { LoginComponent } from './components/login/login.component';
import { VideoPlayerComponent } from './components/learn/video-player/video-player.component';
import { ServiceWorkerModule } from '@angular/service-worker';
import { environment } from '../environments/environment';
import { FormsModule } from '@angular/forms';
import { CustomHTTPInterceptor } from './services/cHttp.interceptor';
import { PaypalBuyComponent } from './components/paypal-buy/paypal-buy.component';
import { MarkdownModule } from 'ngx-markdown';
import { MdWritterComponent } from './components/md-writter/md-writter.component';
import { WriterProfileComponent } from './components/profiles/writer-profile/writer-profile.component';
import { EditorComponent } from './components/editor/editor.component';
import { CheckoutComponent } from './components/checkout/checkout.component';
import { LearnPersonalizedComponent } from './components/learn/learn-personalized/learn-personalized.component';

@NgModule({
  declarations: [
    AppComponent,
    CheckoutComponent,
    HomeComponent,
    FooterComponent,
    LoginComponent,
    VideoPlayerComponent,
    PaypalBuyComponent,
    MdWritterComponent,
    WriterProfileComponent,
    EditorComponent,
    LearnPersonalizedComponent
  ],
  imports: [
    BrowserModule.withServerTransition({ appId: 'serverApp' }),
    AppRoutingModule,
    FormsModule,
    HttpClientModule,
    ServiceWorkerModule.register('ngsw-worker.js', { enabled: environment.production }),
    MarkdownModule.forRoot({ loader: HttpClient }),

  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: CustomHTTPInterceptor,
      multi: true
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
