import { Component, OnInit, PLATFORM_ID, Inject } from '@angular/core';
import { DalService } from '../../services/dal.service';
import { IMService, NotifTypes, IMEventTypes } from '../../services/im.service';
import { isPlatformBrowser } from '@angular/common';
import { AuthService } from '../../services/auth.service';
import { ActivatedRoute } from '@angular/router';
import { compileBaseDefFromMetadata } from '@angular/compiler';

@Component({
  selector: 'app-md-writter',
  templateUrl: './md-writter.component.html',
  styleUrls: ['./md-writter.component.css']
})
export class MdWritterComponent implements OnInit {
  articleModel: any = {
    blogData: '',
    title: '',
    description: '',
    image: '',
    keywords: 'ProCo',
    seriesType: '',
    authorId: '',
    author: '',
    status: 'DRAFT'
  }
  blogHelp = `# Tips
  
 #### Speech to text
   - Use speech to text for speeding up your blog writting
   - you can change language any time once speech recognition is done
   - button will show you current state of recognitions

  #### Use of draft facility
   - recover your blogs even if browser reloads
   - blog will be auto saved to your local when you hit 'enter' while writing the blogs
   - if you want to save any SEO contents as well you should come back to editor and hit enter
   - to clear blog contents empty the contents and hit enter, you will see help again 

  #### toggle preview
   - you can toggle preview in editor mode to work without hassle
   - you can see full page preview as well from header men`;
  preview = false;
  isSubmitted = false;
  mode = 'editor'
  seriesList = [];
  isSeriesOpened = false;
  article: any
  editorClass = "col-md-6";
  winRef: any;
  recognizer: any;
  draftStatus = '';
  recognitionState = 'Start Speech to Text'
  constructor(private dal: DalService, private auth: AuthService,
    private im: IMService,
    @Inject(PLATFORM_ID) private platformId: Object,
    private route: ActivatedRoute) {
    this.configureSpeechToText()
  }
  toggleSpeech() {
    try {
      this.recognizer.start();
    } catch (error) {
      this.recognizer.stop();
    }

    console.log(this.recognizer)
  }

  changeLanguage(lang) {

    this.recognizer.lang = lang
    console.log('changed', this.recognizer.lang)
  }

  configureSpeechToText() {
    this.winRef = window;
    this.winRef.SpeechRecognition = (window as any).webkitSpeechRecognition || (window as any).SpeechRecognition;
    this.recognizer = new (window as any).SpeechRecognition();
    this.recognizer.interimResults = false;
    this.recognizer.maxAlternatives = 10;
    this.recognizer.continuous = true;
    this.recognizer.lang = 'en-IN'

    this.recognizer.onstart = ($event) => {
      this.recognitionState = 'Speak now'
    }

    this.recognizer.onnomatch = ($event) => {

      this.recognitionState = 'Sorry, try again'
      setTimeout(() => {

        this.recognitionState = 'Start Speech to Text'
      }, 3000);
    }

    this.recognizer.onerror = ($event) => {
      this.recognitionState = 'Sorry, try again'
      setTimeout(() => {

        this.recognitionState = 'Start Speech to Text'
      }, 3000);
    }
    this.recognizer.onend = ($event) => {
      this.recognitionState = 'Start Speech to Text'
    }

    this.recognizer.onaudiostart = ($event) => {
      this.recognitionState = 'Listening'
    }

    this.recognizer.onspeechend = ($event) => {
      this.recognitionState = 'Recognizing'
    }

    this.recognizer.onresult = (event) => {
      let interimTranscript = '';
      for (let i = event.resultIndex, len = event.results.length; i < len; i++) {
        let transcript = event.results[i][0].transcript;
        if (event.results[i].isFinal) {
          this.articleModel.blogData += transcript;
          this.recognitionState = 'Start Speech to Text'
          if (transcript.trim().length == 0) {
            this.recognitionState = 'Sorry, try again'
            setTimeout(() => {

              this.recognitionState = 'Start Speech to Text'
            }, 3000);
          }
          // this.recognizer.stop();
        } else {
          interimTranscript += transcript;
          console.log(interimTranscript)
        }
      }

    }
  }

  saveDraft() {
    this.draftStatus = 'saving draft...';

    this.saveArticle(() => {
      this.im.publish(IMEventTypes.NOTIFICATION, { msg: 'Saved as draft', type: NotifTypes.MESSAGE })
      setTimeout(() => {
        this.draftStatus = ''
      }, 5000);
    })


  }
  ngOnInit() {
    this.route.params.subscribe(params => {
      const { articleId } = params;
      if (articleId && articleId.length) {
        this.dal.getArticles({ _id: articleId }).subscribe(x => {
          if (x) {
            this.articleModel = x[0] || {};
            this.articleModel.author = this.auth.personName;
          }
        })
      } else {
        this.dal.getUserDetails().subscribe(x => {
          x = x && x[0];
          this.articleModel.authorId = x._id
          console.log(x, this.articleModel)
        })
      }
    });

    this.dal.getSeriesList().subscribe(x => {
      this.seriesList = x;
    })

    // this.getSingleMdFile()
  }
  publishArticle() {
    if (confirm('Are you sure to publish it ?'))
      this.articleModel.status = 'PUBLISHED'
    this.dal.saveArticle(this.articleModel).subscribe(x => {
      alert('Published')
    })
  }
  togglePreview() {
    if (this.editorClass == 'col-md-6') {
      this.editorClass = 'col-md-12'
    } else {
      this.editorClass = "col-md-6";
    }
  }

  saveArticle(cb?) {
    if (this.articleModel.blogData.trim().length < 100 ||
      this.articleModel.title.trim().length < 20 ||
      this.articleModel.title.trim().length > 70 ||
      this.articleModel.description.trim().length < 20 ||
      this.articleModel.description.trim().length > 370 ||
      this.articleModel.image.trim().length < 20 ||
      this.articleModel.keywords.trim().length < 10) {
      this.im.publish(IMEventTypes.NOTIFICATION, { msg: 'Please fill title (min 20-70 char), blog (min 100), desc (min 20-370), valid image and keyword (min 10 char)', type: NotifTypes.ERROR })

    } else {
      if (this.articleModel._id) {
        this.articleModel.__v++;
      }
      this.dal.saveArticle(this.articleModel).subscribe(x => {
        console.log(x)
        if (x._id) {
          if (cb) {
            cb();
          } else {
            this.im.publish(IMEventTypes.NOTIFICATION, { msg: 'Please check your article in blogs ', type: NotifTypes.SUCCESS })
          }
        }
      })
    }

  }
}
