import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { environment } from '../../../environments/environment';
import { IMService, NotifTypes, IMEventTypes } from '../../services/im.service';
import { DalService } from '../../services/dal.service';
import { Injectable, PLATFORM_ID, Inject } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';

declare let paypal: any;

@Component({
  selector: 'app-paypal-buy',
  templateUrl: './paypal-buy.component.html',
  styleUrls: ['./paypal-buy.component.css']
})
export class PaypalBuyComponent implements OnInit, OnChanges {
  @Input() showBuyModal = false;
  @Input() courseCount = 2;
  @Input() amount: number = 0;
  showSuccess: boolean;
  currency = environment.paypal.currency;

  public payPalConfig: any;

  constructor(private im: IMService, private dal: DalService, @Inject(PLATFORM_ID) private platformId: Object) { }
  ngOnChanges() {
    this.amount = 1
    if (isPlatformBrowser(this.platformId)) {
      paypal.Button.render(this.initConfig(), '#paypal-checkout-btn');
    }
  }

  ngOnInit(): void {

  }
  private initConfig(): void {
    const currency = environment.paypal.currency;
    this.payPalConfig = {

      currency: currency,
      env: 'production',
      client: {
        sandbox: environment.paypal.sandboxClientId,
        production: environment.paypal.prodClientId
      },
      commit: true,
      payment: (data, actions) => {
        return actions.payment.create({
          payment: {
            transactions: [
              { amount: { total: this.amount, currency: environment.paypal.currency } }
            ]
          }
        });
      },

      onApprove: (data, actions) => {
        console.log('onApprove - transaction was approved, but not authorized', data, actions);
        actions.order.get().then(details => {
          console.log('onApprove - you can get full order details inside onApprove: ', details);
          const success = {
            courseCount: this.courseCount,
            currency: this.currency,
            amount: this.amount,
            ...details,
            createdOn: new Date().getTime()
          }
          console.log(success);
          this.showBuyModal = false;

          this.dal.postPaypalSuccess(success).subscribe(x => {
            this.im.publish(IMEventTypes.NOTIFICATION, { msg: 'Thank you for purchasing, please choose courses you want in next page', type: NotifTypes.SUCCESS })
          })
        });



      },
      onClientAuthorization: (data) => {
        console.log('onClientAuthorization - you should probably inform your server about completed transaction at this point', data);
        this.showSuccess = true;
      },
      onCancel: (data, actions) => {
        console.log('OnCancel', data, actions);
      },
      onError: err => {
        console.log('OnError', err);
      },
      onClick: (data, actions) => {
        console.log('onClick', data, actions);
      }
    };
    // console.log(this.payPalConfig)
    return this.payPalConfig;
  }

}
