import { Component, OnInit } from '@angular/core';
import EditorJS from '@editorjs/editorjs';

import Header from '@editorjs/header';
import List from '@editorjs/list';
import Marker from '@editorjs/marker';
import Paragraph from '@editorjs/paragraph';
import ImageTool from '@editorjs/image';
import Quote from '@editorjs/quote';
import LinkTool from '@editorjs/link';
import InlineCode from '@editorjs/inline-code';
import CodeTool from '@editorjs/code';

import { AuthService } from '../../services/auth.service';
import { DalService } from '../../services/dal.service';




@Component({
  selector: 'app-editor',
  templateUrl: './editor.component.html',
  styleUrls: ['./editor.component.css']
})
export class EditorComponent implements OnInit {
  serverHost = 'http://localhost:8440';
  editorInstance = null;
  isAnalyzing = false;
  analysisResult: any;

  articleModel: any = {
    blocks: [],
    time: Date,
    description: '',
    version: '',
    source: 'editorJs'
  }
  constructor(private auth: AuthService, private dal: DalService) { }

  ngOnInit() {
    const data = JSON.parse(localStorage.getItem('blog') || null) as any
    this.editorInstance = new EditorJS({
      placeholder: 'Let`s write an awesome story!',
      holderId: 'codex-editor',
      tools: {
        header: {
          class: Header,
          shortcut: 'CMD+SHIFT+H',
        },
        paragraph: {
          class: Paragraph

        },
        Marker: {
          class: Marker,
          shortcut: 'CMD+SHIFT+M',
        },
        inlineCode: {
          class: InlineCode,
          shortcut: 'CMD+SHIFT+C',
        },
        code: CodeTool,
        list: {
          class: List,
        },
        image: {
          class: ImageTool,
          config: {
            endpoints: {
              byFile: `${this.serverHost}/upload?t=${this.auth.userToken}`, // Your backend file uploader endpoint
              byUrl: `${this.serverHost}/upload?t=${this.auth.userToken}`, // Your endpoint that provides uploading by Url
            }
          }
        },
        quote: {
          class: Quote,
          inlineToolbar: true,
          shortcut: 'CMD+SHIFT+O',
          config: {
            quotePlaceholder: 'Enter a quote',
            captionPlaceholder: 'Quote\'s author',
          },
        },
        linkTool: {
          class: LinkTool,
          config: {
            endpoint: `${this.serverHost}/editor/fetchUrl`, // Your backend endpoint for url data fetching
          }
        },

      },

    });

    this.editorInstance.isReady.then(() => {
      console.log('ready', this.editorInstance)
      this.editorInstance.render(data)
      setTimeout(() => {
        this.makeNonEditable()
      }, 100);
    })
  }


  makeNonEditable() {

    document.querySelectorAll('[contenteditable=true]').forEach(x => {
      x.setAttribute('contenteditable', 'false');
      try {
        this.editorInstance.off(x, 'click', () => { console.log('off event click on ', x) }, false)
      } catch (error) {

      }
    })

  }

  makeEditable() {

    document.querySelectorAll('[contenteditable=false]').forEach(x => {
      x.setAttribute('contenteditable', 'true');
      try {
        this.editorInstance.off(x, 'click', () => { console.log('off event click on ', x) }, false)
      } catch (error) {

      }
    })

  }

  editorToText(cbFun) {
    this.editorInstance.saver.save().then((savedData) => {
      const { blocks } = savedData;
      console.log(savedData, blocks)
      const docText = blocks.map(x => {
        if (x.type !== 'code' && x.type !== 'table')

          return x.data.text || (x.data.items && x.data.items.join('\n')) || null
      }).filter(y => y !== null).join('\n').replace(/&nbsp;/g, ' ')
      cbFun(docText)
    });
    // const { blocks } = { "time": 1577008131791, "blocks": [{ "type": "paragraph", "data": { "text": "Hey" } }, { "type": "header", "data": { "text": "Hello", "level": 2 } }, { "type": "list", "data": { "style": "ordered", "items": ["He", "sdfwes", "sdfSdf", "we"] } }, { "type": "image", "data": { "file": { "url": "http://localhost:8440/uploads/er_mr_bhushan_gmail_com/1577008095119_Screenshot_from_2019-12-21_14-47-47.png" }, "caption": "", "withBorder": false, "stretched": false, "withBackground": false } }, { "type": "quote", "data": { "text": "Hey there", "caption": "", "alignment": "left" } }, { "type": "linkTool", "data": { "link": "https://facebook.com", "meta": { "title": "Facebook - &#x932;&#x949;&#x917; &#x907;&#x928; &#x915;&#x93F;&#x902;&#x935;&#x93E; &#x938;&#x93E;&#x907;&#x928; &#x905;&#x92A;", "site_name": "Facebook", "description": "खाते तयार करा किंवा  Facebook वर  लॉग इन करा.  मित्र, कुटूंब अाणि आपण ओळखत असलेल्‍या अन्‍य लोकांशी कनेक्‍ट व्‍हा. छायाचित्रे आणि व्हिडिओ सामायिक करा,...", "image": { "url": "https://www.facebook.com/images/fb_icon_325x325.png" } } } }], "version": "2.16.1" };

  }
  processReadability(a) {
    return '<b>' + a.actual.replace(/\n/g, '').substring(a.location.start.column - 1, a.location.end.column - 1) + '</b>';
  }

  save() {
    this.editorInstance.saver.save().then((savedData) => {
      this.articleModel.blocks = savedData.blocks;
      this.articleModel.time = savedData.time;
      this.articleModel.version = savedData.version;
      console.log("article model to save", this.articleModel);
      this.dal.saveArticleForEditor(this.articleModel).subscribe(x => {
        alert('article saved');
        console.log("saved data", x)
      })


      localStorage.setItem('blog', JSON.stringify(this.articleModel.blocks));
    });
  }

  openModal = false;
  analyzeDoc() {
    this.isAnalyzing = true;
    this.editorToText((docText) => {
      console.log(docText)
      this.dal.getDocumentAnalysis(docText).subscribe(x => {
        console.log("analyzed text", x);
        this.analysisResult = x;
        this.isAnalyzing = false;
        this.openModal = true;
      }, e => {
        this.isAnalyzing = false;
        alert('Unable to analyze the document, please try again');
      })
    });


  }


  publishArticle() {
    if (confirm('Are you sure to publish it ?'))
      this.articleModel.status = 'PUBLISHED'
    this.dal.saveArticle(this.articleModel).subscribe(x => {
      alert('Published')
    })
  }




}
