import { Component, OnInit } from '@angular/core';
import { DalService } from '../../../services/dal.service';

@Component({
  selector: 'app-writer-profile',
  templateUrl: './writer-profile.component.html',
  styleUrls: ['./writer-profile.component.css']
})
export class WriterProfileComponent implements OnInit {

  authorDetails: any = {}
  performance = {
    comments: 152, articles: 10,
    articleSeries: 2, views: 500
  }
  profile: any = {}
  articles = [];
  constructor(private dal: DalService) { }

  ngOnInit() {
    this.refreshPage();
  }

  refreshPage() {
    this.dal.getUserDetails().subscribe(x => {
      this.authorDetails = x && x[0] || {};
      if (this.authorDetails && this.authorDetails.profile) {
        this.profile = this.authorDetails.profile;
      }
      console.log(this.authorDetails)
      this.dal.getArticles({ status: 'DRAFT', authorId: this.authorDetails._id }).subscribe(x => {
        this.articles = x;
        this.articles.push({
          _id: '',
          title: 'Write New blog',
          image: 'https://images.unsplash.com/photo-1499750310107-5fef28a66643?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=750&q=80',
          description: 'Lets write your first blog'
        });
      })
      this.dal.getArticles({ status: 'PUBLISHED', skipFields: 'image,blogData,description,title,author,routerLink', authorId: this.authorDetails._id }).subscribe(x => {
        console.log(x)
        let views = 0;
        x.forEach(y => views += y.views);
        this.performance = {
          articles: x.length,
          articleSeries: Array.from(new Set(x.map(y => y.seriesType))).length,
          comments: 6,
          views
        }
      });
    })


  }
  updateProfile() {
    this.dal.updateUserProfile({ profile: this.profile }).subscribe(x => {
      alert('updated')
    })
  }

  deleteArticle(articleId) {
    if (confirm('Are you sure to delete this article?')) {
      this.dal.deleteArticle(articleId).subscribe(x => {
        alert('removed')
        this.refreshPage();
      })
    }
  }
}
