import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class SocialShareService {

  constructor() { }

  get domain() {
    return 'https://proco.guru/';
  }

  get popupConfig() {
    return 'toolbar=0,status=0,resizable=yes,width=500,height=600,top=50,left=50';
  }

  get isMobile() {
    if (navigator.userAgent.match(/Android/i)
      || navigator.userAgent.match(/webOS/i)
      || navigator.userAgent.match(/iPhone/i)
      || navigator.userAgent.match(/iPad/i)
      || navigator.userAgent.match(/iPod/i)
      || navigator.userAgent.match(/BlackBerry/i)
      || navigator.userAgent.match(/Windows Phone/i)
    ) {
      return true;
    }
    else {
      return false;
    }
  }

  share(provider: SharingProvider, configOverride: any = {}) {
    let config: any = this.sharingConfigDefault.find(x => x.provider === provider);

    configOverride.text = configOverride.description;
    configOverride.summary = configOverride.description;
    configOverride.redirect_uri = this.domain + configOverride.routerLink;
    configOverride.link = this.domain + configOverride.routerLink;
    configOverride.url = this.domain + configOverride.routerLink;


    if (provider == SharingProvider.TWITTER)
      configOverride.text = configOverride.title;
    if (provider == SharingProvider.WHATSAPP) {
      if (this.isMobile == true) {
        config.baseUrl = "whatsapp://send";
        console.log(config);

      }
      configOverride.text = `Hello, I would like to share ${configOverride.title} with you *on ProCo* ${this.domain + configOverride.routerLink}`;
    }


    if (configOverride)
      Object.keys(configOverride).map(k => {
        console.log(k, config.conf.hasOwnProperty(k), config.conf[k])
        if (config.conf.hasOwnProperty(k)) {
          config.conf[k] = configOverride[k]
        }
      });
    if (provider == SharingProvider.TWITTER) {
      config.conf.hashtags = configOverride.keywords.split(',').map(x => `#${(x || '').trim().replace(/ /g, '_')}`).join(',')
      console.log(config.conf.hashtags)
    }
    if (config.appId) {
      config.conf = { ...config.conf, app_id: config.appId };
    }
    console.log(config, configOverride, provider)
    window.open(config.baseUrl + this.buildQuery(config.conf), 'Share Article', this.popupConfig);
  }

  buildQuery(query?: Object) {
    if (!query) return ""
    return "?" + (Object.keys(query).map(key => key + '=' + query[key]).join('&'))
  }


  get sharingConfigDefault() {
    return [{
      "provider": SharingProvider.EMAIL,
      "conf": { "subject": "", "body": "", "to": "", "cc": "", "bcc": "", "trigger": "click" }
    }, {
      "provider": SharingProvider.FACEBOOK,
      "appId": '752349348510908',
      "baseUrl": 'https://www.facebook.com/dialog/feed',
      "conf": {
        "link": "",
        "title": "",
        "href": "",
        "quote": "",
        "hashtags": "",
        "text": "",
        "media": "",
        "mobile_iframe": "",
        "type": "",
        "via": "",
        "to": "",
        "from": "",
        "ref": "",
        "display": "popup",
        "source": "",
        "caption": "",
        "redirect_uri": "",
        "trigger": "click",
        "popupHeight": 600,
        "popupWidth": 500
      }
    }, {
      "provider": SharingProvider.FACEBOOK_MESSENGER, "conf": { "url": "" }
    },
    {
      "provider": SharingProvider.TWITTER,
      baseUrl: 'https://www.twitter.com/intent/tweet',
      "conf": { "url": "", "text": "", "via": "", "hashtags": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 }
    },
    {
      "provider": SharingProvider.LINKEDIN,
      "baseUrl": 'https://www.linkedin.com/shareArticle',
      "conf": { "url": "", "title": "", "summary": "", "mini": true, "trigger": "click" }
    },
    {
      "provider": SharingProvider.REDDIT,
      "conf": { "url": "", "text": "", "subreddit": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 }
    },
    { "provider": SharingProvider.VK, "conf": { "url": "", "text": "", "media": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }, { "provider": SharingProvider.OK, "conf": { "url": "", "text": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }, { "provider": SharingProvider.DIGG, "conf": { "url": "", "text": "", "media": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }, { "provider": SharingProvider.DELICIOUS, "conf": { "url": "", "text": "", "media": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }, { "provider": SharingProvider.STUMBLEUPON, "conf": { "url": "", "text": "", "media": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }, { "provider": SharingProvider.PINTEREST, "conf": { "url": "", "text": "", "media": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }, { "provider": SharingProvider.GOOGLE, "conf": { "url": "", "text": "", "media": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }, { "provider": SharingProvider.TUMBLR, "conf": { "url": "", "text": "", "media": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }, { "provider": SharingProvider.BUFFER, "conf": { "url": "", "text": "", "via": "", "media": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }, { "provider": SharingProvider.POCKET, "conf": { "url": "", "text": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }, { "provider": SharingProvider.FLIPBOARD, "conf": { "url": "", "text": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }, { "provider": SharingProvider.HACKERNEWS, "conf": { "url": "", "text": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }, { "provider": SharingProvider.WORDPRESS, "conf": { "url": "", "text": "", "media": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }, { "provider": SharingProvider.XING, "conf": { "url": "", "text": "", "media": "", "follow": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }, { "provider": SharingProvider.EVERNOTE, "conf": { "url": "", "text": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } },
    { "provider": SharingProvider.WHATSAPP, baseUrl: 'https://web.whatsapp.com/send', "conf": { "url": "", "text": "" } }, { "provider": SharingProvider.SMS, "conf": { "url": "", "text": "", "to": "", "trigger": "click" } }, { "provider": SharingProvider.TELEGRAM, "conf": { "url": "", "text": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }, { "provider": SharingProvider.VIBER, "conf": { "url": "", "text": "" } }, { "provider": SharingProvider.SKYPE, "conf": { "url": "", "text": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }, { "provider": SharingProvider.WEIBO, "conf": { "url": "", "text": "", "trigger": "click", "popupHeight": 600, "popupWidth": 500 } }];
  }

}

export enum SharingProvider {
  FACEBOOK = "FACEBOOK",
  FACEBOOK_MESSENGER = "FACEBOOK_MESSENGER",
  SMS = "SMS",
  TWITTER = "TWITTER",
  LINKEDIN = "LINKEDIN",
  GOOGLE = "GOOGLE",
  PINTEREST = "PINTEREST",
  TUMBLR = "TUMBLR",
  REDDIT = "REDDIT",
  STUMBLEUPON = "STUMBLEUPON",
  BUFFER = "BUFFER",
  DIGG = "DIGG",
  DELICIOUS = "DELICIOUS",
  VK = "VK",
  POCKET = "POCKET",
  WORDPRESS = "WORDPRESS",
  FLIPBOARD = "FLIPBOARD",
  XING = "XING",
  HACKERNEWS = "HACKERNEWS",
  EVERNOTE = "EVERNOTE",
  WHATSAPP = "WHATSAPP",
  TELEGRAM = "TELEGRAM",
  VIBER = "VIBER",
  SKYPE = "SKYPE",
  EMAIL = "EMAIL",
  OK = "OK",
  WEIBO = "WEIBO"
}